import React, { useEffect, useState } from 'react';
import Chart from 'react-apexcharts';
import 'bootstrap/dist/css/bootstrap.min.css';

export default function CaseGrowth(props){
    const [district,setDistrict] = useState(props.district)
    const [title,setTitle] = useState("Kerala - KL");
    const [count,setCount] = useState([]);
    const [dates,setDates] = useState([]);
    const [isDefault,setIsDefault] = useState(true);
    const [limit,setLimit] = useState();
    const options={
        chart: {
            height: 150,
            type: 'line',
            zoom: {
              type: 'x',
              enabled: true,
              autoScaleXaxis: true
            },
            toolbar: {
              autoSelected: 'zoom'
            }
          },
          dataLabels: {
            enabled: false
          },
            stroke: {
                width: 4,
                curve: 'smooth'
              },
          title: {
            text: 'Confirmed Case Growth',
            align: 'left'
          },
          markers: {
            size: 4,
            colors: ["#FFA41B"],
            strokeColors: "#fff",
            strokeWidth: 2,
            hover: {
              size: 2,
            }
          },
          fill: {
            type: 'gradient',
            gradient: {
              shade: 'dark',
              gradientToColors: [ '#FDD835'],
              shadeIntensity: 1,
              type: 'horizontal',
              opacityFrom: 1,
              opacityTo: 1,
              stops: [0, 60, 60,]
            },
          },
          grid: {
            row: {
              colors: ['#f3f3f3', 'transparent'], // takes an array which will be repeated on columns
              opacity: 0.5
            },
          },
          xaxis: {
            categories: dates,
            title:{
              text:"Date"
            }   
          },
          yaxis:{
            title:{
              text:"Total Cases"
            }          }
        }
        const series =[{
            name: "Cases",
            data:count
        }] 
        const options2={
          chart: {
              height: 150,
              type: 'line',
              zoom: {
                type: 'x',
                enabled: true,
                autoScaleXaxis: true
              },
              toolbar: {
                autoSelected: 'zoom'
              }
            },
            dataLabels: {
              enabled: false
            },
              stroke: {
                  width: 4,
                  curve: 'smooth'
                },
            title: {
              text: 'Confirmed Case Growth',
              align: 'left'
            },
            markers: {
              size: 4,
              colors: ["#FFA41B"],
              strokeColors: "#fff",
              strokeWidth: 2,
              hover: {
                size: 2,
              }
            },
            fill: {
              type: 'gradient',
              gradient: {
                shade: 'dark',
                gradientToColors: [ '#FDD835'],
                shadeIntensity: 1,
                type: 'horizontal',
                opacityFrom: 1,
                opacityTo: 1,
                stops: [0, 60, 60,]
              },
            },
            grid: {
              row: {
                colors: ['#f3f3f3', 'transparent'], // takes an array which will be repeated on columns
                opacity: 0.5
              },
            },
            xaxis: {
              categories: dates.slice(limit),
              title:{
                text:"Date"
              }   
            },
            yaxis:{
              title:{
                text:"Total Cases"
              }          }
          }
          const series2 =[{
              name: "Cases",
              data:count.slice(limit)
          }] 
    useEffect(()=>{
        var date=[];
        let counts = [];
        let total=0;
        var d =(props.district);
        setDistrict(props.district);
        if(props.district===""){
            setTitle("Kerala")
            setLimit(-21)
            fetch('https://api.covid19india.org/states_daily.json').then(resp=>resp.json())
            .then((data)=>{
                (data.states_daily).map((c)=>{
                    if(c.status==='Confirmed'){
                        date.push((c.date).substr(0,6));
                        total=total+Number(c.kl);
                        counts.push(total)
                    }
                })
                setCount(counts);
                setDates(date)
            })
          }
          else{
                //---------------------------------------------------//
          //-----------------------DISTRICTS-------------------//
          //--------------------------------------------------//


            setTitle(props.district);
            setLimit(-7)
            var datesArray=[];
            var countsArray=[];
            fetch('https://api.covid19india.org/districts_daily.json').then(resp=>resp.json())
            .then((data)=>{
              let result=(data.districtsDaily.Kerala)
              if(d==="Alappuzha"){
                result.Alappuzha.map((data,i)=>{
                 datesArray.push(data.date);
                 countsArray.push(data.confirmed)
                })
                setDates(datesArray);
                setCount(countsArray);
              }
              else if(d==="Ernakulam"){
                result.Ernakulam.map((data,i)=>{
                  datesArray.push(data.date);
                  countsArray.push(data.confirmed)
                 })
                 setDates(datesArray);
                 setCount(countsArray);
              }
              else if(d==="Idukki"){
                result.Idukki.map((data,i)=>{
                  datesArray.push(data.date);
                  countsArray.push(data.confirmed)
                 })
                 setDates(datesArray);
                 setCount(countsArray);
              }
              else if(d==="Kannur"){
                result.Kannur.map((data,i)=>{
                  datesArray.push(data.date);
                  countsArray.push(data.confirmed)
                 })
                 setDates(datesArray);
                 setCount(countsArray);
              }
              else if(d==="Kasaragod"){
                result.Kasaragod.map((data,i)=>{
                  datesArray.push(data.date);
                  countsArray.push(data.confirmed)
                 })
                 setDates(datesArray);
                 setCount(countsArray);
              }
              else if(d==="Kollam"){
                result.Kollam.map((data,i)=>{
                  datesArray.push(data.date);
                  countsArray.push(data.confirmed)
                 })
                 setDates(datesArray);
                 setCount(countsArray);              }
              else if(d==="Kottayam"){
                result.Kottayam.map((data,i)=>{
                  datesArray.push(data.date);
                  countsArray.push(data.confirmed)
                 })
                 setDates(datesArray);
                 setCount(countsArray);
              }
              else if(d==="Kozhikode"){
                result.Kozhikode.map((data,i)=>{
                  datesArray.push(data.date);
                  countsArray.push(data.confirmed)
                 })
                 setDates(datesArray);
                 setCount(countsArray);
              }
              else if(d==="Malappuram"){
                result.Malappuram.map((data,i)=>{
                  datesArray.push(data.date);
                  countsArray.push(data.confirmed)
                 })
                 setDates(datesArray);
                 setCount(countsArray);             }
              else if(d==="Palakkad"){
                result.Palakkad.map((data,i)=>{
                  datesArray.push(data.date);
                  countsArray.push(data.confirmed)
                 })
                 setDates(datesArray);
                 setCount(countsArray);
              }
              else if(d==="Pathanamthitta"){
                result.Pathanamthitta.map((data,i)=>{
                  datesArray.push(data.date);
                  countsArray.push(data.confirmed)
                 })
                 setDates(datesArray);
                 setCount(countsArray);              }
              else if(d==="Thiruvananthapuram"){
                result.Thiruvananthapuram.map((data,i)=>{
                  datesArray.push(data.date);
                  countsArray.push(data.confirmed)
                 })
                 setDates(datesArray);
                 setCount(countsArray);
              }
              else if(d==="Thrissur"){
                result.Thrissur.map((data,i)=>{
                  datesArray.push(data.date);
                  countsArray.push(data.confirmed)
                 })
                 setDates(datesArray);
                 setCount(countsArray);
              }
              else if(d==="Wayanad"){
                result.Wayanad.map((data,i)=>{
                  datesArray.push(data.date);
                  countsArray.push(data.confirmed)
                 })
                 setDates(datesArray);
                 setCount(countsArray);
              }
           
              })
        



                //---------------------------------------------------//
          //-----------------------DISTRICTS-------------------//
          //--------------------------------------------------//
          
          }
    },[props.district])
const setDefault = ()=>{setIsDefault(true)}
const setlimit = () => {setIsDefault(false)}
    return(
  
        <React.Fragment>
             <nav className = "nav nav-tabs nav-fill flex-column flex-sm-row  bg-dark text-white mb-2 ">
            <ul className = "nav activatable p-2 d-flex justify-content-center" >
          <li class = "nav-item" >
            <a class = "nav-link pointer-without-bg"
            onClick={setDefault} >Default </a> </li><li class = "nav-item" >
            <a class = "nav-link pointer-without-bg"
            onClick={setlimit} > Last {limit} Days </a> </li>  </ul>
 
            </nav> 
           {(isDefault && <Chart options={options} series={series} type="line" height={350} />)}
           
           {(isDefault===false && <Chart options={options2} series={series2} type="line" height={350} />)}
        </React.Fragment>
    )
}