import React, { useEffect, useState } from 'react';
import { Accordion , Card ,Button} from 'react-bootstrap';
import NationalComparison from './nationalcomparison';
import CaseGrowth from './casegrowth';
import DailyChanges from './dailychanges';
import Test from './tests';
import DistrictPerformance from './distPerform';
import DistInfo from './distInfo';
export default  function Analysis(props){
    const [district,setDistrict] = useState(props.district);
    const [title,setTitle] = useState("Kerala - KL");
    const [compTitle,setCompTitle] = useState("National Level  Comparision")
    const [type,setType] =useState(true);
    const [dailyTitle,setDailyTitle] = useState("Daily Changes");
    const [testTitle,setTestTitle] = useState("Tests - Confirmations");
    useEffect(()=>{
        setDistrict(props.district)
        console.log(props.district)
        if(props.district===""){
          setTitle("Kerala")
        }
        else{
          //---------------------------------------------------//
          //-----------------------DISTRICTS-------------------//
          //--------------------------------------------------//
          setTitle(props.district);
          setCompTitle("State Level  Comparision")
          setType(false);
          setDailyTitle("Confirmations - Recoveries")
          

            //---------------------------------------------------//
          //-----------------------DISTRICTS-------------------//
          //--------------------------------------------------//
        }
     
    },[props.district])

    return(
        <React.Fragment>
           <div className="container-fluid">
           <Accordion defaultActiveKey="0">
  <Card>
    <Card.Header>
      <Accordion.Toggle as={Button} variant="link" eventKey="0" style={{'textDecoration':'none'}}>
       {compTitle}
      </Accordion.Toggle>
    </Card.Header>
    <Accordion.Collapse eventKey="0">
      <Card.Body>
          <NationalComparison district={props.district}/>
      </Card.Body>
    </Accordion.Collapse>
  </Card>
  <Card>
    <Card.Header>
      <Accordion.Toggle as={Button} variant="link" eventKey="1" style={{'textDecoration':'none'}}>
        Case Growth Analysis
      </Accordion.Toggle>
    </Card.Header>
    <Accordion.Collapse eventKey="1">
      <CaseGrowth district={props.district}/>
    </Accordion.Collapse>
  </Card>
  <Card>
    <Card.Header>
      <Accordion.Toggle as={Button} variant="link" eventKey="2" style={{'textDecoration':'none'}}>
        {dailyTitle}
      </Accordion.Toggle>
    </Card.Header>
    <Accordion.Collapse eventKey="2">
      <Card.Body>
        {(type && <DailyChanges district={district}/>)}
        {(type===false && <DistrictPerformance district={district}/>)}
        
        </Card.Body>
    </Accordion.Collapse>
  </Card>
 
  {(type &&  <Card>
    <Card.Header>
      <Accordion.Toggle as={Button} variant="link" eventKey="3" style={{'textDecoration':'none'}}>
      {testTitle}
      </Accordion.Toggle>
    </Card.Header>
    <Accordion.Collapse eventKey="3">
      <Card.Body>
      <Test district={district}/>
       

      </Card.Body>
    </Accordion.Collapse>
  </Card> )}
</Accordion>
           </div>
        </React.Fragment>
    )
}