import React, { useEffect } from 'react';
import { useState } from 'react';
import './progress.css';
import { CircularProgressbar,buildStyles} from 'react-circular-progressbar';
import Test from './tests';

export default function NationalComparison(props){


    const [district,setDistrict] = useState(props.district);
    const [type,setType] = useState(true);
    const [deathP,setDeathP] = useState(0);
    const [activeP,setactiveP] = useState(0);
    const [recoveryP,SetRecoveryP] = useState(0);
    const [totalact,settotalact] = useState(0);
    const [title,setTitle] = useState("Kerala - KL");
    const [parent,setParent] = useState("India");
    useEffect(()=>{
      let stateConfirm,stateRec,stateDeath,stateActive;
      var d = props.district;
        setDistrict(props.district)

        fetch('https://api.covid19india.org/data.json').then((resp)=>resp.json())
        .then((data)=>{
         stateConfirm= (data.statewise[16].confirmed);
         stateDeath = (data.statewise[16].deaths);
         stateRec =(data.statewise[16].recovered);
         stateActive = (data.statewise[16].active);

        });


        if(props.district===""){
          setTitle("Kerala");
          fetch('https://api.covid19india.org/data.json').then((resp)=>resp.json())
          .then((data)=>{
        
            var latestData = Number(data.cases_time_series.length);
            var Activep = (((data.statewise[16].confirmed)/(data.cases_time_series[latestData-1].totalconfirmed))*100);
             setactiveP(Activep.toPrecision(3))
             var deathP =(((data.statewise[16].deaths)/(data.cases_time_series[latestData-1].totaldeceased))*100);
                setDeathP(deathP.toPrecision(3));
          var nationRecoveryRate = ((data.cases_time_series[latestData-1].totalrecovered)/(data.cases_time_series[latestData-1].totalconfirmed))*100;
              nationRecoveryRate.toPrecision(3);
        var stateRecoveryRate = (((data.statewise[16].recovered)/(data.statewise[16].confirmed))*100).toPrecision(3);
            var RecoveryRate = (stateRecoveryRate-nationRecoveryRate).toPrecision(3);
          SetRecoveryP(RecoveryRate)
        }).then(()=>{
               
            })
          
        }
        else{
          setType(false);
          setParent("Kerala")
          setTitle(props.district)
          var distConf;
var distRecovered;
var distDeath;
var distActive;
          fetch('https://api.covid19india.org/v2/state_district_wise.json').then((resp)=>resp.json())
              .then((data)=>{
              data.map((state,i)=>{
                if(state.state==="Kerala"){
                 var result = (state.districtData)
                    result.map((dist,i)=>{
                      if(dist.district===d){
                        distConf = Number(dist.confirmed);
                         distRecovered = Number(dist.recovered);
                         distDeath = Number(dist.deceased);
                         distActive = Number(dist.active);
                      }
                    })
                    setactiveP(((distActive/stateActive)*100).toPrecision(2))
                     SetRecoveryP(((distRecovered/stateRec)*100).toPrecision(2))
                     settotalact(((distConf/stateConfirm)*100).toPrecision(2))
                     setDeathP(((distDeath/stateDeath)*100).toPrecision(2))
                }
              })
              })
          
           
        }
    },[props.district])
    


    return(
        <React.Fragment>
            <div className="d-flex justify-content-center">
    <p className="text-dark font-weight-bold font-large">{title} Has</p>
            </div>
       
                  <div className="row">
                    
                    
                     {( type && <div className="col">
                  <div className="box">
                <CircularProgressbar
                  value={activeP}
                  text={`${activeP}%`}
                  styles={buildStyles({
                    textColor: "#33d9b2",
                    pathColor: "#218c74",
                    
                  })}
                />
                </div>
              <div className="text-dark text-center text-capitalize font-weight-bold smallfont">{activeP} % of total confirmed cases in {parent}</div>
                </div>)}

                {(type===false && <div className="col">
                  <div className="box">
                <CircularProgressbar
                  value={activeP}
                  text={`${activeP}%`}
                  styles={buildStyles({
                    textColor: "#33d9b2",
                    pathColor: "#218c74",
                    
                  })}
                />
                </div>
              <div className="text-dark text-center text-capitalize font-weight-bold smallfont">{activeP} % of total active cases in {parent}</div>
                </div>)}

             
             
             
             
                <div className="col">
                  <div className="box">
                <CircularProgressbar
                  value={deathP}
                  text={`${deathP}%`}
                  styles={buildStyles({
                    textColor: "#33d9b2",
                    pathColor: "#218c74",
                    
                  })}
                />
                </div>
              <div className="text-dark text-center text-capitalize font-weight-bold smallfont">{deathP} % of total expires in {parent}</div>
                </div>

               
               
               
              
              {( type &&  <div className="col">
                  <div className="box">
                <CircularProgressbar
                  value={recoveryP}
                  text={`${recoveryP}%`}
                  styles={buildStyles({
                    textColor: "#33d9b2",
                    pathColor: "#218c74",
                    
                  })}
                
                />
                </div>
              <div className="text-dark text-center text-capitalize font-weight-bold smallfont">{recoveryP} % more Recovery rate than national level</div>
                </div>)}


{(  type===false &&
                <div className="col">
                  <div className="box">
                <CircularProgressbar
                  value={recoveryP}
                  text={`${recoveryP}%`}
                  styles={buildStyles({
                    textColor: "#33d9b2",
                    pathColor: "#218c74",
                    
                  })}
                
                />
                </div>
              <div className="text-dark text-center text-capitalize font-weight-bold smallfont">{recoveryP} % Recoveries of total reported in Kerala</div>
                </div>
)}


{(  type===false &&
                <div className="col">
                  <div className="box">
                <CircularProgressbar
                  value={totalact}
                  text={`${totalact}%`}
                  styles={buildStyles({
                    textColor: "#33d9b2",
                    pathColor: "#218c74",
                    
                  })}
                
                />
                </div>
              <div className="text-dark text-center text-capitalize font-weight-bold smallfont">{totalact} % total confirmed Cases in Kerala</div>
                </div>
)}








                </div>
          
        </React.Fragment>
    )
}