import React, { useEffect } from 'react';
import { useState } from 'react';
import Chart from 'react-apexcharts';

export default function Test(props){

    const [district,setDistrict] = useState(props.district)
    const [title,setTitle] = useState("Kerala - KL");
    const [dates,setDates] = useState([]);
    const [tests,setTests] = useState([]);
    const [confirms,setConfirms] = useState([]);
    const [isDefault,setIsDefault] = useState(true);
    const setlimit = () =>{setIsDefault(false)}
    const setDefault = () => {setIsDefault(true)}
    const [totaltests,settt] = useState();
    const [negatives,setnegatives] = useState();
    const [unconfirmed,setunconfirmed] = useState();
    const options=
    {
        chart: {
          height: 350,
          type: 'line',
        },
        stroke: {
          width: [0, 4]
        },
        title: {
          text: 'Total Tests And Confirmation Ratio'
        },
        dataLabels: {
          enabled: true,
          enabledOnSeries: [1]
        },
        labels:dates,
        xaxis: {
          type: 'string'
        },
        yaxis: [{
          title: {
            text: 'Total Tests Conducted',
          },
        
        }, {
          opposite: true,
          title: {
            text: 'Total Confirmations'
          }
        }]
      
    
    
    };
        
        
        const series =[{
            name: 'Tests',
            type: 'column',
            data: tests
          }, {
            name: 'Confirms',
            type: 'line',
            data: confirms
          }];



          const options2=
          {
              chart: {
                height: 350,
                type: 'line',
              },
              stroke: {
                width: [0, 4]
              },
              title: {
                text: 'Total Tests And Confirmation Ratio'
              },
              dataLabels: {
                enabled: true,
                enabledOnSeries: [1]
              },
              labels:dates.slice(-21),
              xaxis: {
                type: 'string'
              },
              yaxis: [{
                title: {
                  text: 'Total Tests Conducted',
                },
              
              }, {
                opposite: true,
                title: {
                  text: 'Total Confirmations'
                }
              }]
            
          
          
          };
              
              
              const series2 =[{
                  name: 'Tests',
                  type: 'column',
                  data: tests.slice(-21)
                }, {
                  name: 'Confirms',
                  type: 'line',
                  data: confirms.slice(-21)
                }];

    useEffect(()=>{
        var testcol=[];
        var dateArray=[];
        var total=241;
        var counts =[]
      
        setDistrict(props.district);
        if(props.district===""){
            setTitle("Kerala")
            fetch('https://api.covid19india.org/state_test_data.json').then(res=>res.json())
            .then(data=>{
                var tt,un,ne =0 ;
               data.states_tested_data.map((item,i)=>{
                    if(item.state === "Kerala"){
                            dateArray.push((item.updatedon))
                            testcol.push(item.totaltested)
                            tt = (item.totaltested);
                            ne = (item.negative);
                            un = (item.unconfirmed);
                    }
                })
                settt(tt);
                            setnegatives(ne);
                            setunconfirmed(un);
              
            })
         
            fetch('https://api.covid19india.org/states_daily.json').then(resp=>resp.json())
            .then((data)=>{
                data=data.states_daily.slice(53);
                (data).map((c)=>{
                    if(c.status==='Confirmed'){
                        total=total+Number(c.kl);
                        counts.push(total)
                    }
                })
              
                setConfirms(counts);
                setDates(dateArray);
                setTests(testcol)
            })
           
           
            
            
        }
          else{
            setTitle(props.district);
          
          }
    },[props.district])

    return(
        <React.Fragment>
             <nav className = "nav nav-tabs nav-fill flex-column flex-sm-row bg-dark text-white mb-2">
            <ul className = "nav activatable p-2 d-flex justify-content-center" >
          <li class = "nav-item" >
            <a class = "nav-link pointer-without-bg"
            onClick={setDefault} >Default </a> </li><li class = "nav-item" >
            <a class = "nav-link pointer-without-bg"
            onClick={setlimit} > Last 21 Days </a> </li>  </ul>
 
            </nav> 
           {(isDefault && <Chart options={options} series={series} type="line" height={350} />)}
           
           {(isDefault===false && <Chart options={options2} series={series2} type="line" height={350} />)}

           <div className="row  d-flex justify-content-around">
             <div className="col">
               <div className="text-secondary font-weight-bold">Total Tests Conducted :
               <span className="text-dark">
                 {totaltests}</span> </div></div>

                 <div className="col">
                 <div className="text-secondary font-weight-bold">Negatives :
               <span className="text-dark">
                 {negatives}</span> </div></div>
            
                 <div className="col"> <div className="text-secondary font-weight-bold">Unconfirmed :
               <span className="text-dark">
                 {unconfirmed}</span> </div></div>
            
             </div>
           
        </React.Fragment>
    )
}