import React, { useEffect } from 'react';
import { useState } from 'react';
import Chart from 'react-apexcharts';

export default function DailyChanges(props){
    const [district,setDistrict] = useState(props.district)
    const [title,setTitle] = useState("Kerala - KL");
    const [Reportedcount,setReportedCount] = useState([]);
    const [Recovereddcount,setRecoveredCount] = useState([]);
    const [counts,setCount] = useState();
    const [dates,setDates] = useState([]);
    const options={
        chart: {
            height: 150,
            type: 'bar',
            zoom: {
              enabled: false
            }
          },
          dataLabels: {
            enabled: false
          },
          stroke: {
            curve: 'straight'
          },
          title: {
            text: 'Daily Case Growth From 14 March',
            align: 'left'
          },
          grid: {
            row: {
              colors: ['#f3f3f3', 'transparent'], // takes an array which will be repeated on columns
              opacity: 0.5
            },
          },
          xaxis: {
            categories: dates,
           
          },
          yaxis:{
            title:{
              text:"Confirmed - Recovered"
            }   
          }
        }
        const series =[{
            name: "Cases",
            data:counts
        }] 
    useEffect(()=>{
        var date=[];
        let rep = [];
        let rec = [];
        setDistrict(props.district);
        if(props.district===""){
            setTitle("Kerala")
            fetch('https://api.covid19india.org/states_daily.json').then(resp=>resp.json())
            .then((data)=>{
                (data.states_daily).map((c)=>{
                    if(c.status==='Confirmed'){
                        date.push((c.date).substr(0,6));
                        rep.push(c.kl)
                    }
                    if(c.status==="Recovered"){
                        rec.push(c.kl)
                    }
                })
              var result = (rep.map((value,i)=>{
                  return  value - rec[i]
              }))
              setCount(result)
              setDates(date)
            })
          }
          else{
            setTitle(props.district);
            var datesArray=[];
            var countsArray=[];
            let  d= props.district;
            fetch('https://api.covid19india.org/districts_daily.json').then(resp=>resp.json())
            .then((data)=>{
              let result=(data.districtsDaily.Kerala)
              if(d==="Alappuzha"){
                result.Alappuzha.map((data,i)=>{
                 datesArray.push(data.date);
                 
                 countsArray.push(data.confirmed)
                })
                setDates(datesArray);
                setCount(countsArray);
              }
              else if(d==="Ernakulam"){
                result.Ernakulam.map((data,i)=>{
                  datesArray.push(data.date);
                  countsArray.push(data.confirmed)
                 })
                 setDates(datesArray);
                 setCount(countsArray);
              }
              else if(d==="Idukki"){
                result.Idukki.map((data,i)=>{
                  datesArray.push(data.date);
                  countsArray.push(data.confirmed)
                 })
                 setDates(datesArray);
                 setCount(countsArray);
              }
              else if(d==="Kannur"){
                result.Kannur.map((data,i)=>{
                  datesArray.push(data.date);
                  countsArray.push(data.confirmed)
                 })
                 setDates(datesArray);
                 setCount(countsArray);
              }
              else if(d==="Kasaragod"){
                result.Kasaragod.map((data,i)=>{
                  datesArray.push(data.date);
                  countsArray.push(data.confirmed)
                 })
                 setDates(datesArray);
                 setCount(countsArray);
              }
              else if(d==="Kollam"){
                result.Kollam.map((data,i)=>{
                  datesArray.push(data.date);
                  countsArray.push(data.confirmed)
                 })
                 setDates(datesArray);
                 setCount(countsArray);              }
              else if(d==="Kottayam"){
                result.Kottayam.map((data,i)=>{
                  datesArray.push(data.date);
                  countsArray.push(data.confirmed)
                 })
                 setDates(datesArray);
                 setCount(countsArray);
              }
              else if(d==="Kozhikode"){
                result.Kozhikode.map((data,i)=>{
                  datesArray.push(data.date);
                  countsArray.push(data.confirmed)
                 })
                 setDates(datesArray);
                 setCount(countsArray);
              }
              else if(d==="Malappuram"){
                result.Malappuram.map((data,i)=>{
                  datesArray.push(data.date);
                  countsArray.push(data.confirmed)
                 })
                 setDates(datesArray);
                 setCount(countsArray);             }
              else if(d==="Palakkad"){
                result.Palakkad.map((data,i)=>{
                  datesArray.push(data.date);
                  countsArray.push(data.confirmed)
                 })
                 setDates(datesArray);
                 setCount(countsArray);
              }
              else if(d==="Pathanamthitta"){
                result.Pathanamthitta.map((data,i)=>{
                  datesArray.push(data.date);
                  countsArray.push(data.confirmed)
                 })
                 setDates(datesArray);
                 setCount(countsArray);              }
              else if(d==="Thiruvananthapuram"){
                result.Thiruvananthapuram.map((data,i)=>{
                  datesArray.push(data.date);
                  countsArray.push(data.confirmed)
                 })
                 setDates(datesArray);
                 setCount(countsArray);
              }
              else if(d==="Thrissur"){
                result.Thrissur.map((data,i)=>{
                  datesArray.push(data.date);
                  countsArray.push(data.confirmed)
                 })
                 setDates(datesArray);
                 setCount(countsArray);
              }
              else if(d==="Wayanad"){
                result.Wayanad.map((data,i)=>{
                  datesArray.push(data.date);
                  countsArray.push(data.confirmed)
                 })
                 setDates(datesArray);
                 setCount(countsArray);
              }
           
              })
          
          }
    },[props.district])


    return(
        <React.Fragment>
        <Chart options={options} series={series} type="bar" height={350} />
        </React.Fragment>
    )

}