import React, { useEffect, useState } from 'react';
import { Badge } from "react-bootstrap";
import 'bootstrap/dist/css/bootstrap.min.css';
import './data.css'
import {CSSTransition} from 'react-transition-group';
import Analysis from './analys';
export default function Data(props){
    const [district,setDistrict] = useState(props.district);
    const [fetchedData,setFetchedData] = useState([]);
    const [title,setTitle] = useState("Kerala - KL");
    const [lastUpdated,setLastUpdated] = useState("Undefined")
    const [inprop,setInprop] = useState(false);
    const [typevar,settypevar] = useState(true);
    const [zone,setZone] = useState();
   
    useEffect(()=>{
        setDistrict(props.district)
        //fetching API
        if(props.district==="")
       { 

          fetch('https://api.covid19india.org/data.json')
        .then((resp)=>resp.json())
        .then((data)=>{
          
         setFetchedData(data.statewise[16])
            setLastUpdated(data.statewise[16].lastupdatedtime)
        }).then(()=>{
            setInprop(true)
        })
        
    } //If ""Overview""
    else{
        const d = props.district;
        setTitle(props.district)
        settypevar(false);
        fetch('https://api.covid19india.org/districts_daily.json').then(resp=>resp.json())
        .then((data)=>{
          let result=(data.districtsDaily.Kerala)
          if(d==="Alappuzha"){
            setFetchedData((result.Alappuzha).slice(-1)[0])
            console.log(result.Alappuzha.slice(-1))
          }
          else if(d==="Ernakulam"){
            setFetchedData(result.Ernakulam.slice(-1)[0])
          }
          else if(d==="Idukki"){
            setFetchedData(result.Idukki.slice(-1)[0])
          }
          else if(d==="Kannur"){
            setFetchedData(result.Kannur.slice(-1)[0])
          }
          else if(d==="Kasaragod"){
            setFetchedData(result.Kasaragod.slice(-1)[0])
          }
          else if(d==="Kollam"){
            setFetchedData(result.Kollam.slice(-1)[0])
          }
          else if(d==="Kottayam"){
            setFetchedData(result.Kottayam.slice(-1)[0])
          }
          else if(d==="Kozhikode"){
            setFetchedData(result.Kozhikode.slice(-1)[0])
          }
          else if(d==="Malappuram"){
            setFetchedData(result.Malappuram.slice(-1)[0])
          }
          else if(d==="Palakkad"){
            setFetchedData(result.Palakkad.slice(-1)[0])
          }
          else if(d==="Pathanamthitta"){
            setFetchedData(result.Pathanamthitta.slice(-1)[0])
          }
          else if(d==="Thiruvananthapuram"){
            setFetchedData(result.Thiruvananthapuram.slice(-1)[0])
          }
          else if(d==="Thrissur"){
            setFetchedData(result.Thrissur.slice(-1)[0])
          }
          else if(d==="Wayanad"){
            setFetchedData(result.Wayanad.slice(-1)[0])
          }
       
          })
          fetch('https://api.covid19india.org/zones.json').then((resp)=>resp.json())
          .then((data)=>{
              data.zones.map((dist,i)=>{
                  if(dist.district===props.district){
                      setZone(dist.zone)
                  }
              })
          })
    
    }

    },[props.district])
    return(
        <React.Fragment>
            <div className="container-fluid">
           
                <div className="row m-3 d-flex justify-content-center">
                    <div className="title"> {title} </div>
                </div>

                {(typevar===false &&
                    <div className="row">
                       <div className="col d-flex justify-content-center"><div className=" font-weight-bold" style={{'fontSize':'18px'}}>
                The District Is Under : <span style={{'color':zone,'fontFamily':'Segoe UI'}}>{zone}</span> Zone
                       </div>
                        </div>
                        </div>
                    )}

                <div className="row ">
                    <div className="col text-center text-secondary font-weight-bold m-2">Last Updated : {lastUpdated} </div>
                </div>
         <CSSTransition 
         in={inprop}
         timeout={1000}
         classNames="fade">
                <div className="row">
                    <div className="col mb-3 mr-3 d-flex justify-content-around">
                        <div className="badge">
                            <Badge variant="primary">Total Reported<br/>{fetchedData.confirmed}</Badge>
                        </div>
                    </div>

                    <div className="col mb-3 mr-3 d-flex justify-content-around">
                        <div className="badge">
                            <Badge variant="success">Total Recovered<br/>{fetchedData.recovered}</Badge>
                        </div>
                    </div>

                    <div className="col mb-3 mr-3 d-flex justify-content-around">
                        <div className="badge">
                            <Badge variant="danger">Total Deaths<br/>
                            {(typevar && fetchedData.deaths)}
                            {(typevar===false && fetchedData.deceased )}
                            </Badge>
                        </div>
                    </div>

                    <div className="col mb-3 mr-3 d-flex justify-content-around">
                        <div className="badge text-white">
                            <Badge variant="info">Under Treatment<br/>{fetchedData.active}</Badge>
                        </div>
                    </div>
                </div>
               </CSSTransition>
            </div>
            <Analysis district={props.district}/>
            
        </React.Fragment>
    )
}