// eslint-disable-next-line
import React, { useState } from 'react';
import './search.css'
import Autocomplete from 'react-autocomplete';
import Data from './data';
export default function Search(){
    const [text,setText] = useState("")
    const style={
       ' border': '1px solid black',
        'borderRadius': '20px',
       ' padding': '3px',
        'textIindent': '4px',
        'color': 'rgb(9, 41, 75)',
        'fontWeight': 'bold',
        'textTransform': 'uppercase',
    }
    return(
        <React.Fragment>
            <div className="container-fluid">
                <div className="row">
                    <div className="col d-flex justify-content-center m-4">
                        <div className="search d-flex">
                            <div className="text-success font-weight-bold mr-4">
                                District
                            </div>
                            <Autocomplete
                                        
                                            getItemValue={(item) => item.label}
                                            items={[
                                                { label: 'Alappuzha',id:1 },
                                                { label: 'Ernakulam',id:2 },
                                                { label: 'Idukki',id:3 },
                                                { label: 'Kannur',id:4 },
                                                { label: 'Kasaragod',id:5 },
                                                { label: 'Kollam',id:6 },
                                                { label: 'Kottayam',id:7 },
                                                { label: 'Kozhikode',id:8 },
                                                { label: 'Malappuram',id:9 },
                                                { label: 'Palakkad',id:10 },
                                                { label: 'Pathanamthitta',id:11 },
                                                { label: 'Thiruvananthapuram',id:12 },
                                                { label: 'Thrissur',id:13 },
                                                { label: 'Wayanad',id:14 },
                                                
                                            ]}
                                            renderItem={(item, isHighlighted) =>
                                                <div style={{ background: isHighlighted ? 'lightgray' : 'white' }} key={item.id}>
                                                {item.label}
                                                </div>
                                            }
                                            menuStyle={style}
                                            value={text}
                                            onChange={e => {setText(e.target.value)}}
                                            onSelect={value => setText(value)}
                                            />
                        </div>
                    </div>
                </div>
            </div>

            <Data district ={text}/>
        </React.Fragment>
    )
}