import React, { useEffect, useState } from 'react';
import Chart from 'react-apexcharts';
export default function DistrictPerformance(props){

const [district,setDistrict] = useState(props.district);
const [dates,setDates] = useState();
const [count,setCount] = useState();
const [recovery,setRecovery] = useState();

const series = [
    {
      name: "Confirmations",
      data: count
    },
    {
      name: "Recoveries",
      data: recovery
    }
  ];
  const options = {
    chart: {
      height: 350,
      type: 'line',
      dropShadow: {
        enabled: true,
        color: '#000',
        top: 18,
        left: 7,
        blur: 10,
        opacity: 0.2
      },
      toolbar: {
        show: false
      }
    },
    colors: ['#77B6EA', '#27ae60'],
    dataLabels: {
      enabled: true,
    },
    stroke: {
      curve: 'smooth'
    },
    title: {
      text: 'Confirmations - Recoveries:Performance',
      align: 'left'
    },
    grid: {
      borderColor: '#e7e7e7',
      row: {
        colors: ['#f3f3f3', 'transparent'], // takes an array which will be repeated on columns
        opacity: 0.5
      },
    },
    markers: {
      size: 1
    },
    xaxis: {
      categories: dates,
      title: {
        text: 'Date'
      }
    },
    yaxis: {
      title: {
        text: 'Values'
      },
     
    },
    legend: {
      position: 'top',
      horizontalAlign: 'right',
      floating: true,
      offsetY: -25,
      offsetX: -5
    }
  }




useEffect(()=>{
    var d=props.district;
setDistrict(props.district)
    if(props.district===""){
        return null;
    }
    else{
        setDistrict(props.district)
        var datesArray=[];
        var countsArray=[];
        var recArray =[];
  
        fetch('https://api.covid19india.org/districts_daily.json').then(resp=>resp.json())
        .then((data)=>{
          let result=(data.districtsDaily.Kerala)
          if(d==="Alappuzha"){
            result.Alappuzha.map((data,i)=>{
             datesArray.push(data.date);
             countsArray.push(data.confirmed)
             recArray.push(data.recovered)
            })
            setDates(datesArray);
            setCount(countsArray);
            setRecovery(recArray)
          }
          else if(d==="Ernakulam"){
            result.Ernakulam.map((data,i)=>{
              datesArray.push(data.date);
              countsArray.push(data.confirmed);
              recArray.push(data.recovered)
            })
            setDates(datesArray);
            setCount(countsArray);
            setRecovery(recArray)
          }
          else if(d==="Idukki"){
            result.Idukki.map((data,i)=>{
                datesArray.push(data.date);
                countsArray.push(data.confirmed);
                recArray.push(data.recovered)
              })
              setDates(datesArray);
              setCount(countsArray);
              setRecovery(recArray)
          }
          else if(d==="Kannur"){
            result.Kannur.map((data,i)=>{
                datesArray.push(data.date);
                countsArray.push(data.confirmed);
                recArray.push(data.recovered)
              })
              setDates(datesArray);
              setCount(countsArray);
              setRecovery(recArray)
          }
          else if(d==="Kasaragod"){
            result.Kasaragod.map((data,i)=>{
                datesArray.push(data.date);
                countsArray.push(data.confirmed);
                recArray.push(data.recovered)
              })
              setDates(datesArray);
              setCount(countsArray);
              setRecovery(recArray)
          }
          else if(d==="Kollam"){
            result.Kollam.map((data,i)=>{
                datesArray.push(data.date);
                countsArray.push(data.confirmed);
                recArray.push(data.recovered)
              })
              setDates(datesArray);
              setCount(countsArray);
              setRecovery(recArray)        }
          else if(d==="Kottayam"){
            result.Kottayam.map((data,i)=>{
                datesArray.push(data.date);
                countsArray.push(data.confirmed);
                recArray.push(data.recovered)
              })
              setDates(datesArray);
              setCount(countsArray);
              setRecovery(recArray)
          }
          else if(d==="Kozhikode"){
            result.Kozhikode.map((data,i)=>{
                datesArray.push(data.date);
                countsArray.push(data.confirmed);
                recArray.push(data.recovered)
              })
              setDates(datesArray);
              setCount(countsArray);
              setRecovery(recArray)
          }
          else if(d==="Malappuram"){
            result.Malappuram.map((data,i)=>{
                datesArray.push(data.date);
                countsArray.push(data.confirmed);
                recArray.push(data.recovered)
              })
              setDates(datesArray);
              setCount(countsArray);
              setRecovery(recArray)          }
          else if(d==="Palakkad"){
            result.Palakkad.map((data,i)=>{
                datesArray.push(data.date);
                countsArray.push(data.confirmed);
                recArray.push(data.recovered)
              })
              setDates(datesArray);
              setCount(countsArray);
              setRecovery(recArray)
          }
          else if(d==="Pathanamthitta"){
            result.Pathanamthitta.map((data,i)=>{
                datesArray.push(data.date);
                countsArray.push(data.confirmed);
                recArray.push(data.recovered)
              })
              setDates(datesArray);
              setCount(countsArray);
              setRecovery(recArray)          }
          else if(d==="Thiruvananthapuram"){
            result.Thiruvananthapuram.map((data,i)=>{
                datesArray.push(data.date);
                countsArray.push(data.confirmed);
                recArray.push(data.recovered)
              })
              setDates(datesArray);
              setCount(countsArray);
              setRecovery(recArray)
          }
          else if(d==="Thrissur"){
            result.Thrissur.map((data,i)=>{
                datesArray.push(data.date);
                countsArray.push(data.confirmed);
                recArray.push(data.recovered)
              })
              setDates(datesArray);
              setCount(countsArray);
              setRecovery(recArray)
          }
          else if(d==="Wayanad"){
            result.Wayanad.map((data,i)=>{
                datesArray.push(data.date);
                countsArray.push(data.confirmed);
                recArray.push(data.recovered)
              })
              setDates(datesArray);
              setCount(countsArray);
              setRecovery(recArray)
          }
       
          })
    }
},[props.district])

    return(
        <React.Fragment>
       
       <Chart options={options} series={series} type="line" height={350} />
        </React.Fragment>
    )
}